// httpåå§å
const envApiPaths = {
  'development': '/api.aspx',
  'production': '/api.aspx',
  'app-development': 'http://fn.91zhengxin.com:9111/api.aspx',
  'app-production': 'http://fn.91zhengxin.com:9111/api.aspx'
}
