import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '../store'
import { includes } from 'lodash'
import { RET_CODE_MAP, CUST_STATE_CODE_MAP } from '../constants.js'
import { Toast } from 'mint-ui'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  base: __dirname,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes
})

// ä¸å¡ç¶æå¯¹åºé¡µé¢æ å°è¡¨
const custStateRedirectMap = {
  [CUST_STATE_CODE_MAP.NOT_LOGIN]: { name: 'login' }, // æªç»å½
  [CUST_STATE_CODE_MAP.DEBT_SETTELED]: { name: 'borrowerInfo' }, // åæ¬¾ç»æ¸
  [CUST_STATE_CODE_MAP.CONTRACT_INFO_FILLED]: { name: 'signature' }, // ååä¿¡æ¯å·²å®æ
  [CUST_STATE_CODE_MAP.NOT_INVITED]: { name: 'unauthorizedTip' }, // æªéè¯·
  [CUST_STATE_CODE_MAP.LOANING]: { name: 'loaning' }, // æ¾æ¬¾ä¸­
  [CUST_STATE_CODE_MAP.LOAN_FAILED]: { name: 'loanFailed' }, // æ¾æ¬¾å¤±è´¥
  [CUST_STATE_CODE_MAP.DEBT_NOT_SETTLED]: { name: 'repayInfo' }, // åæ¬¾æªç»æ¸
  [CUST_STATE_CODE_MAP.REPAYING]: { name: 'repaying' }, // è¿æ¬¾ä¸­
  [CUST_STATE_CODE_MAP.REPAY_FAILED]: { name: 'repayFailed' }, // è¿æ¬¾å¤±è´¥
  [CUST_STATE_CODE_MAP.BLACKLIST]: { name: 'blacklistTip' }, // é»ååå®¢æ·
  [CUST_STATE_CODE_MAP.FIRST_BORROWER]: { name: 'authorizedTip' }, // é¦æ¬¡åæ¬¾
  [CUST_STATE_CODE_MAP.UNKNOWN]: { name: 'login' } // æªç¥ç¶æ
}

function getRedirectRoute(stateCode) {
  return custStateRedirectMap[stateCode] || { name: 'login' }
}

router.beforeEach((to, from, next) => {
  const { user, token, stateCode } = store.getters
  if (!to.meta.skipAuth) { // éè¦ç»å½æéçé¡µé¢
    if (!token || !user.phone) {
      next({ name: 'login' })
    } else {
      const stateCodePromise = new Promise((resolve, reject) => {
        if (stateCode) {
          resolve(stateCode)
        } else {
          store.dispatch('getCustStateCode').then(res => {
            if (res.ret === RET_CODE_MAP.OK) {
              resolve(res.data.StateCode)
              if (res.data.StateCode === CUST_STATE_CODE_MAP.CONTRACT_INFO_FILLED) {
                Toast({
                  message: 'æ¨æä¸ç¬ååå·²æäº¤ä½æ²¡æç­¾ç½²'
                    // position: 'top'
                })
              }
              // ç¶æå¼å¸¸è´¦æ·
              if (res.data.StateCode === CUST_STATE_CODE_MAP.UNKNOWN) {
                Toast({
                  message: 'è´¦æ·ç¶æå¼å¸¸ï¼'
                })
              }
            } else {
              reject(null)
            }
          })
        }
      })

      stateCodePromise.then(code => {
        if (includes(to.meta.permits, code)) {
          next()
        } else {
          next(getRedirectRoute(code))
        }
      }).catch(() => {
        next({ name: 'login' })
      })
    }
  } else { // ä¸éè¦æéçé¡µé¢ä¸æ¦æª
    next()
  }
})

router.afterEach((to) => {
  if (to.name !== undefined) {
    document.body.setAttribute('class', to.name)
  }

  if (to.meta.title) {
    document.title = to.meta.title
    document.body.setAttribute('page', to.name)

    // hack ios title not update bug
    const iframe = document.createElement('iframe')
    iframe.classList.add('dn')
    iframe.src = require('../assets/images/clear.png')
    document.body.appendChild(iframe)
    iframe.onload = () => {
      setTimeout(() => {
        iframe.onload = null
        document.body.removeChild(iframe)
      }, 10)
    }
  }
})

export default router



